import React, { Component } from 'react';
import {Link} from 'react-router-dom'

class SignalLabel extends Component {
    constructor(props){
      super(props)
      this.state = {
        list: [
          {
            aid: 111,
            title: "列表1"
          },
          {
            aid: 222,
            title: "列表2"
          },
          {
            aid: 333,
            title: "列表3"
          },
          {
            aid: 444,
            title: "列表4"
          },
        ]
      }
    }
    render() {
        return (
          <div className = "SignalLabel" >
            <h2>通过路由传值</h2>
            <ul>
              {
                this.state.list.map((value,key)=>{
                  return (
                    <li key={key}><Link to={`/equipment/detail/${value.aid}`}>{value.title}</Link></li>
                  )
                })
              }
            </ul>
            <h2>通过get传值</h2>
            <ul>
              {
                this.state.list.map((value,key)=>{
                  return <li key={key}><Link to={`/equipment/detailget/?aid=${value.aid}`}>{value.title}</Link></li>
                })
              }
            </ul>
          </div>
        )
    }
}
//to={`/equipment/signallabel/detail/${value.aid}`}
export default SignalLabel;
